

.GameCard {
    width: auto;
    max-width: 200px;
    height: 300px;
    border: 1px solid #ffe555;
    border-radius: 3px;
}
.inActive {
    opacity: 0.5;
}
.statu-text {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    transform: rotate(-45deg);
    font-size: 32px;
    color: azure;
}
.show-status {
    display: none;
}
.roulette_american {
    background-image: url("../../../assets/gameCards/roulette_american.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
}
