.SignUp{
    width:300px;
    height: 450px;
    border: 2px solid black;
    background-color: blanchedalmond;
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 20;

}
.SignUp .header{
    width: 300px;
    height: 30px;
    background-color: black;
    color: #dbdbdb;
    text-align: center;
}
.SignUp .header button{
    width: 25px;
    height: 24px;
    margin-top: 3px;
    margin-right: 3px;
    float: right;
}