.InfoBlock {
    width: 100vw;
    height: 650px;
    background-image: url("../../../assets/bg_intro.jpg");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    border-radius: 3px;
    margin-top: 10px;
}
.InfoBlock .content {
    display: none;
}
