.InfoBlock .text{
  width: 90%;
  margin-left: 5%;
  color: #dbdbdb;
  position: relative;
  bottom: 0px;
  text-align: center;
  font-size: 22px;
  
}


@media only screen and (max-width:600px){
  .InfoBlock{
    /* background-image: url("../../../assets/bg_intro_m.jpg"); */
    background-image: url("../../../assets/bg_intro_m.png");
    background-size: 90% 90%;
    background-repeat: no-repeat;
  }
} 











@keyframes flicker {
    0%, 18%, 22%, 25%, 53%, 57%, 100% {
      text-shadow:
        0 0 4px #fff,
        0 0 11px #fff,
        0 0 19px #fff,
        0 0 40px #0fa,
        0 0 80px #0fa,
        0 0 90px #0fa,
        0 0 100px #0fa,
        0 0 150px #0fa;
    }
    20%, 24%, 55% {       
      text-shadow: none;
    }
  }