.betPanel {
    width: 90vw;
    height: 120px;
    margin-left: 5vw;
    /* border: 1px solid black; */
    position: absolute;
    bottom: 20px;
    left: 0px;
    display: flex;
}

.betPanel .item {
    width: calc(10% - 10px);
    height: calc(100% - 0px);
    margin-left: 5px;
    margin-right: 5px;
    /* border: 2px solid #ffe555; */
    display: flex;
    justify-content: center;
}
.betPanel .itemBg {
    width: 100%;
    height: 100%;
    max-width: 90px;
    background-image: url("../../../../assets/fortuneSpin/betButton.png");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    display: block;
    justify-content: center;
}

.betPanel .betNumber {
    width: 100%;
    height: 40px;
    font-size: 30px;
    color: #ffe555;
    text-align: center;
}
.betPanel .betAmount {
    width: 100%;
    font-size: 36px;
    color: #000000;
    text-align: center;
}
.betPanel .betAmount:hover {
    cursor: pointer;
}
.betPanel .placeBet {
    font-size: 16px;
}

@media only screen and (max-width: 600px) {
    .betPanel {
        height: 160px;
        bottom: 10px;
        display: block;
    }
    .betPanel .list1,
    .list2 {
        width: 100%;
        height: 50%;
        display: flex;
    }
    .betPanel .item {
        width: calc(20% - 10px);
        height: calc(100% - 5px);
        margin-left: 5px;
        margin-right: 5px;
        margin-bottom: 5px;
    }
    .betPanel .betNumber {
        width: 100%;
        height: 25px;
        font-size: 20px;
    }
    .betPanel .betAmount {
        font-size: 24px;
    }
    .betPanel .placeBet {
        width: 80%;
        margin-left: 10%;
        text-align: center;
        font-size: 14px;
        word-wrap: break-word;
    }
}
