.profile-label{
    width: auto;
    height: 120px;
    position: fixed;
    top: 0%;
    right: 10px;
    z-index: 11;
}


.profile-label .player-count{
    position: absolute;
    right: 350px;
    top: 90px;
    color: #dbdbdb;
}

.profile-label .login-section {
    display: flex;
    position: absolute;
    right: 20px;
    top: 40px;
}
.profile-label .login-section .btn {
    width: 100px;
    height: 35px;
    background-image: url("../../../assets/button.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    color: #ffe555;
    margin-left: 15px;
    text-align: center;
}
.profile-label .login-section .btn:hover {
    cursor: pointer;
    /* transform: scale(0.95); */
    border: 2px solid rgba(255, 255, 255, 0.2);
    border-radius: 3px;
    margin-left: 11px;

}


.profile-section {
    width:400px;
    height: 105px;
    /* border: 1px solid red ; */
    position: absolute;
    right: -60px;
    top: 10px;
    display: flex;
    z-index: 3;


}

.profile-section .profile-avtar {
    width: 95px;
    height: 95px;
    border-radius: 50%;
    border: 5px solid #0e5c63;
    background-image: url("../../../assets/avtars/avtar_icon1.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    z-index: 2;
    background-color: black;
}
.profile-section .profile-avtar:hover {
    transform: scale(1.05);
    cursor: pointer;
}
.profile-section .avtar-border{
    width: 80px;
    height: 105px;
    border-radius: 30px 0px 0px 30px;
    background-color: #0e5c63;
    position: relative;
    left: -70px;
}
.profile-section .profile-content{
    width: 220px;
    height: 95px;
    border: 5px solid #0e5c63;
    position: relative;
    left: -68px;
    border-radius: 0px 10px 10px 0px;
    color: #dbdbdb;
}
.profile-section .profile-content .userName{
    width: auto;
    height: 20px;
    border-bottom: 1px solid #0e5c63;
}
.profile-section .profile-content .balance{
    width: auto;
    height: 20px;
    border-bottom: 1px solid #0e5c63;
}
.profile-section .profile-content .balance-inGame{
    width: auto;
    height: 23px;
}
.profile-section .profile-content .btns{
    width: auto;
    height: 30px;
    display: flex;
}
.profile-section .profile-content .btns .cashier{
    width: calc(100% - 80px);
    height: 100%;
    background-image: url("../../../assets/cashierBtn.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    font-size: 18px;
    text-align: center;
    color: black;
    margin-top: -3px;
    margin-left: 3px;

}
.profile-section .profile-content .btns .settings{
    width: 37px;
    height: 37px;
    background-image: url("../../../assets/icons/setting.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    margin-top: -5px;
}
.profile-section .profile-content .btns .logout{
    width: 35px;
    height: 35px;
    background-image: url("../../../assets/icons/logOut.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    margin-top: -6px;

}
.profile-section .profile-content .btns .cashier:hover{
    transform: scale(1.05);
    cursor: pointer;

}
.profile-section .profile-content .btns .settings:hover{
    transform: scale(1.08);
    cursor: pointer;

}
.profile-section .profile-content .btns .logout:hover{
    transform: scale(1.08);
    cursor: pointer;

}


@media screen and (max-width: 600px) {
   
    .profile-label .login-section {
        display: flex;
        position: absolute;
        right: 5px;
        top: 20px;
        /* float: right;
        margin-right: 20px; */
    }
    .profile-label .login-section .btn {
        width: 80px;
        height: 35px;
        background-image: url("../../../assets/button.png");
        background-repeat: no-repeat;
        background-size: 100% 100%;
        color: #ffe555;
        margin-left: 8px;
        text-align: center;
    }
}