.PromoBlock {
    width: 100vw;
    height: 300px;
    background-color: #000000;
    /* margin-top: 10px; */
}
.PromoBlock .title{
    width: 90%;
    height: auto;
    margin-left: 5%;
    color: #dbdbdb;
    font-size: 22px;
    text-align: center;
    margin-bottom: 10px;
}
.PromoBlock .content {
    width: 300px;
    height: fit-content;
    color: #dbdbdb;
    float: left;
    margin-left: 20px;
}
.PromoBlock .promotion {
    width: 500px;
    height: 250px;
    color: #dbdbdb;
    float: right;
    /* margin-right: 20px; */
    /* border: 1px solid yellow; */
    display: flex;
}
.PromoBlock .promotion .text-block {
    width: 60%;
    height: 90%;
    border: 5px solid rgb(255, 229, 85, 0.75);
    border-radius: 10px;
    text-align: center;
    padding: 5px;
}
.PromoBlock .promotion .image {
    width: 40%;
    height: 90%;
    background-image: url("../../../assets/msgBoard.png");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    position: relative;
    left: -50px;
    /* border: 1px solid red; */
}

@media screen and (max-width: 480px) {
    .PromoBlock .title{
        /* margin-top: 20px; */
        margin-bottom: 0px;
    }
    .PromoBlock .content {
        display: none;
    }
    .PromoBlock .promotion {
        width: 100%;
        height: 200px;
        margin-top: 40px; 
    }
    .PromoBlock .promotion .image {
        left: -15px;
    }
    .PromoBlock .promotion .text-block {
        position: relative;
        right: -25px;
    }
}
