.stats{
    width: 280px;
    height: 200px;
    position: absolute;
    right: 10px;
    top: 210px;
    border: 2px solid rgb(255, 229, 85,0.75);
    background-color: rgb(0, 0, 0,0.75);
    border-radius: 3px;
}
.stats .title{
    width:100%;
    height: 25px;
    background-color: rgba(0, 0, 0,0.75);
    color: #ffe555;
    text-align: center;
    border-bottom: 1px solid rgba(255, 229, 85,0.75);
}
.stats .body{
    width: 100%;
    height: 175px;
}
.stats .body tbody{
    height: 115px;
    display: block;
    /* overflow-y: scroll; */
}
.stats .body .h{
    /* height: 25px; */
    background-color: #0f212f;
}
.stats .body .row{
    width: 280px;
    /* height: 25px; */
    display: table;
}
.stats .body .row2{
    height: 22px;
    font-size: 14px;

}
.stats .body .col-date{
    width: 150px;
    height: 20px;
    text-align: left;
}
.stats .body .col{
    width: 65px;
    height: 20px;
    text-align: center;
}
