
.Games{
    width: 100vw;
    height: fit-content;
    display: inline;
    color: #dbdbdb;
}
.game-cards-grid{
    width: 100vw;
    height: 650px;
    background-color: black;
    /* background-image: url("../../../assets/lobby.jpg");
    background-size: 100% 100%;
    background-repeat: no-repeat; */
    display: grid;
    grid-template-columns: repeat(5,200px);
    gap: 10px;
    overflow-y: scroll;
    justify-content: center;
    padding-top: 20px;
    padding-bottom: 20px;
}
.Games h1,h2{
    display: flex;
    justify-content: center;
    /* align-items: center; */
    /* align-content: center; */
}
.Games h1 {
    color: #ffe555;
    animation: animateText 1s infinite ease-in-out;
}


@media only screen and (max-width:600px) {
    .game-cards-grid{
        width: 94vw;
        height: auto;
        margin-left: 3vw;
        display: grid;
        grid-template-columns: repeat(2,1fr);
        gap: 10px;
    }
}


@keyframes animateText {
    0% {color: #dbdbdb;}
    50% {color: #00ff00;}
    100% {color: #ffe555;}
}