.infoBoard{
    width: 280px;
    height: 50px;
    position: absolute;
    right: 10px;
    top: 150px;
    border: 2px solid rgb(255, 229, 85,0.75);
    background-color: rgb(0, 0, 0,0.75);
    /* text-align: center; */
    display: flex;
    justify-content: center;
    align-items: center;
    /* align-content: center; */
    border-radius: 3px;
}

.won {
    color: #ffe555;
}
.lost {
    color: #ff0000;
}

@media only screen and (max-width: 600px) {

.infoBoard{
    top: 95px;
    left: 50%;
    transform: translateX(-50%);
}
    
}