.Login{
    width: 300px;
    height: 400px;
    border: 2px solid black;
    background-color: blanchedalmond;
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 20;
}

.Login .header{
    width: 300px;
    height: 30px;
    background-color: black;
    color: #dbdbdb;
    text-align: center;
}
.Login .header button{
    width: 25px;
    height: 24px;
    margin-top: 3px;
    margin-right: 3px;
    float: right;
}
.Login .content{
    width: calc(100% - 2px);
    height: calc(100% - 34px);
    display: flex;
    /* justify-content: center; */
    align-items: center;
    /* align-content: center; */
}

.Login .content input[type="text"]{
    width: 250px;
    height: 30px;
    margin-bottom: 10px;
    margin-left: 25px;
}
.Login .content input[type="password"]{
    width: 250px;
    height: 30px;
    margin-bottom: 10px;
    margin-left: 25px;


}
.Login .content input[type="submit"]{
    width: 150px;
    height: 30px;
    margin-left: 75px;

}


