.fortuneWheel {
    width: 450px;
    height: 450px;
    position: absolute;
    left: 50%;
    top: 50px;
    transform: translateX(-50%);
}
.fortuneWheel .spinWheelGraphics {
    width: 400px;
    height: 400px;
    margin: 25px;
    /* position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%); */
    /* -webkit-animation: rotate 20s infinite linear; */
    /* animation: rotate 10s infinite linear; */
}
.fortuneWheel .wheelOuter {
    width: 100%;
    height: 100%;
    /* background-image: url("./assets/spinWheel.png"); */
    background-image: url("../../../../assets/fortuneSpin/spinWheel.png");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    position: relative;
    top: -545px;
}
.fortuneWheel .wheelBase {
    width: 100%;
    height: 95px;
    /* background-image: url("./assets/base.png"); */
    background-image: url("../../../../assets/fortuneSpin/base.png");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    position: relative;
    top: -30px;
}
.fortuneWheel .wedgePlate {
    width: 400px;
    height: 400px;
    border: 1px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    border-radius: 50%;
}
.fortuneWheel .numberPlate {
    width: 400px;
    height: 400px;
    /* margin: 25px; */
    border-radius: 50%;
    position: relative;
    top: -418px;
}

.fortuneWheel .wedgeContainer {
    width: 400px;
    height: 400px;
    position: relative;
}

.fortuneWheel .wedge {
    position: absolute;
    transform-origin: 100% 100%;
    background: #ccc;
    /* width: 200px; */
    width: 50%;
    /* height: 200px; */
    height: 50%;
    left: 50%;
    top: 50%;
    border: 1px solid #999;
    /* margin-top: -200px; */
    margin-top: -50%;
    /* margin-left: -200px; */
    margin-left: -50%;
    list-style: none;
}

.fortuneWheel .wedge:nth-child(even) {
    background-color: yellow;
}

.fortuneWheel .wedge:nth-child(odd) {
    background-color: aqua;
}

.fortuneWheel .number {
    width: 50px;
    /* height: 200px; */
    height: 50%;
    /* border: 1px solid red; */
    display: inline-block;
    text-align: center;
    position: absolute;
    top: 0;
    left: calc(50% - 25px);
    transform-origin: 50% 100%;
    background-color: transparent;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-content: center;
}
.fortuneWheel .number span {
    width: 50px;
    height: 80px;
    color: rgb(20, 17, 17);
    font-size: 36px;
    position: absolute;
    top: 0px;
}

.fortuneWheel .spinWheelGraphics[spin-wheel="0"] {
    /* animation: rotate 10s ease-in-out; */
    transform: rotate(2520deg);
    transition: transform 9s cubic-bezier(0.28, 0.48, 0.2, 1);
}
