.chipsPanel {
    width: 240px;
    /* height: 240px; */
    min-height: 35px;
    height: auto;
    position: absolute;
    left: 10px;
    bottom: 30vh;
    background-color: rgba(0,0,0,0.75);
    border: 2px solid rgb(255, 229, 85,0.75);

}
.chipsPanel .header{
    text-align: center;
}
.chipsPanel .row {
    width: 100%;
    height: 80px;
    display: flex;
}
.chipsPanel .column {
    width: 80px;
    height: 80px;
    /* border: 1px solid #ffe555; */
}

.chipsPanel .chip {
    width: 80%;
    height: 80%;
    /* background-image: url("../../../../assets/fortuneSpin/chips/chip_1.png"); */
    background-repeat: no-repeat;
    background-size: 100% 100%;
    margin-left: 10%;
    margin-top: 10%;
}
.chipsPanel .active {
    transform: scale(0.75);
}
.chipsPanel .chip:hover {
    cursor: pointer;
    transform: scale(1.05);
}
.chipsPanel .base {
    display: block;
    width: 100%;
    height: 15px;
    background-image: url("../../../../assets/baseLine.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    justify-content: center;
    /* margin-left: 10%; */
}
.chipsPanel .flip{
    transform: rotate(180deg);
}

.chipsPanel .chip_1 {
    background-image: url("../../../../assets/fortuneSpin/chips/chip_1.png");
}
.chipsPanel .chip_5 {
    background-image: url("../../../../assets/fortuneSpin/chips/chip_5.png");
}
.chipsPanel .chip_10 {
    background-image: url("../../../../assets/fortuneSpin/chips/chip_10.png");
}
.chipsPanel .chip_50 {
    background-image: url("../../../../assets/fortuneSpin/chips/chip_50.png");
}
.chipsPanel .chip_100 {
    background-image: url("../../../../assets/fortuneSpin/chips/chip_100.png");
}
.chipsPanel .chip_500 {
    background-image: url("../../../../assets/fortuneSpin/chips/chip_500.png");
}
.chipsPanel .chip_1000 {
    background-image: url("../../../../assets/fortuneSpin/chips/chip_1000.png");
}
.chipsPanel .chip_5000 {
    background-image: url("../../../../assets/fortuneSpin/chips/chip_5000.png");
}
.chipsPanel .chip_1 {
    background-image: url("../../../../assets/fortuneSpin/chips/chip_1.png");
}

.chipsPanel .show {
    display: block;
}
.chipsPanel .hide {
    display: none;
}

