.Contact {
    width: 100vw;
    height: calc(100vh - 120px);
    background-color: #000000;
    animation: contactPageAnim 0.2s 1 ease-in-out;
}

.Contact .page-body {
    width: 100vw;
    height: calc(100vh - 290px);
    display: flex;
}
.Contact .Footer {
    width: 100vw;
    height: 150px;
    background-image: url("../../../assets/footer-bg.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    color: #dbdbdb;
}

.Contact .page-body .contentBox{
    width: calc(100vw - 330px);
    height: calc(100% - 20px);
    /* background-color: #4e524f; */
    background-image: url("../../../assets/contact-bg.jpg");
    background-repeat: no-repeat;
    background-size: 100% 100%;

    border-radius: 5px;
    margin-left: 10px;
    margin-top: 10px;
    margin-right: 10px;


}
.Contact .page-body .form{
    width: 300px;
    height: calc(100% - 20px);
    /* background-color: #4e524f; */
    margin-top: 10px;
    margin-right: 10px;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Contact .page-body form{
    width: 80%;
    height: 250px;
}
.Contact .page-body form input{
    width: 100%;
    height: 30px;
    outline: none;
    margin-bottom: 10px;
    border-radius: 3px;

}
.Contact .page-body form textarea{
    width: 100%;
    height: 70px;
    overflow-y: scroll;
    /* margin-left: 10%; */

}
.Contact .page-body form input[type="submit"]{
    width: 50%;
    height: 30px;
    margin-left: 25%;
    background-image: url("../../../assets/button.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    border: none;
    color: #ffe555;
    font-size: 1rem;

}


.Contact .Footer .title{
    float: left;
    margin-left: 10px;
}
.Contact .Footer .info{
    float: right;
    margin-right: 20px;
}
.Contact .Footer .info .field{
    width: auto;
    height: 25px;
    display: flex;
}
.Contact .Footer .info .field span{
    margin-left: 10px;
    margin-top: 5px;
  
}
.Contact .Footer .info .in-icon{
    width: 25px;
    height: 25px;
    background-image: url("../../../assets/icons/in-icon.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
}
.Contact .Footer .info .phone-icon{
    width: 25px;
    height: 25px;
    background-image: url("../../../assets/icons/phone-icon.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
}
.Contact .Footer .info .email-icon{
    width: 25px;
    height: 25px;
    background-image: url("../../../assets/icons/email-icon.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
}




@media only screen and (min-width: 600px) {
.Contact .Footer .info{
    margin-right: 100px;
    margin-top: 30px;
}
.Contact .Footer .title{
    margin-top: 20px;
    font-size: 24px;
}
   
}
@media only screen and (max-width: 600px) {
    .Contact .page-body .contentBox{
        display: none;
    }
.Contact .page-body .form{
    width: calc(100vw - 20px);
    height: calc(100% - 20px);
    margin-left: 10px;
}

}

@keyframes contactPageAnim {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
