.games-lobby-btn{
    width: 100px;
    height: 35px;
    background-image: url("../../../assets/button.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    color: #ffe555;
    padding: 0px 5px;
    display: flex;
    text-align: center;
    justify-content: center;
    align-content: center;
    align-items: center;
    position: absolute;
    left: 10px;
    top: 20px;
}


.btn_style1 {
    width: 100px;
    height: 35px;
    background-color: #0f212f;
    border: 2px solid rgb(255, 229, 85,0.75);
    border-radius: 5px 0px 5px 0px;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}

.btn_swipe{
    width: 22px;
    height: 88px;
    background-image: url("../../../assets/swipeBtn.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    position: absolute;
    right: 0px;
    bottom: 300px;
    z-index: 98;
}
.btn_swipe_rot{
    transform: rotate(180deg);
}




.games-lobby-btn,
.btn_style1:hover{
    cursor: pointer;
}