.mobileMenu{
    width: 300px;
    height: 100vh;
    background-color: rgba(0,0,0,0.75);
    border-left: 2px solid rgb(255, 229, 85,0.75);
    position: absolute;
    top: 0;
    right: 0;
    z-index: 98;
}

.mobileMenu .profile-section{
    width: 280px;
    position: relative;
    left: 5px;
    top: 10px;
}
.mobileMenu .profile-section .profile-content{
    height: 80px;
    left: 0px;
}
.mobileMenu .profile-section .profile-content .userName {
    height: 25px;
}
.mobileMenu .profile-section .profile-content .balance {
    height: 25px;
}
.mobileMenu .profile-section .profile-content .balance-inGame {
    height: 25px;
}

.mobileMenu .profile-section .profile-avtar{
   width: 80px;
   height: 80px;
}
.mobileMenu .profile-section .avtar-border{
    left: -2px;
    width: 110px;
    height: 90px;
    border-radius: 40px 0px 0px 40px;
}