.Footer{
    width: 100vw;
    height: 150px;
    /* background-image: url("../../../assets/footer-bg.png");
    background-repeat: no-repeat;
    background-size: 100% 100%; */
    background-color: #000000;
    color: #dbdbdb;
    margin-top: 10px;
}
.Footer .title{
    float: left;
    margin-left: 10px;
}
.Footer .info{
    float: right;
    margin-right: 20px;
}
.Footer .info .field{
    width: auto;
    height: 25px;
    display: flex;
}
.Footer .info .field span{
    margin-left: 10px;
    margin-top: 5px;
  
}
.Footer .info .in-icon{
    width: 25px;
    height: 25px;
    background-image: url("../../../assets/icons/in-icon.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
}
.Footer .info .phone-icon{
    width: 25px;
    height: 25px;
    background-image: url("../../../assets/icons/phone-icon.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
}
.Footer .info .email-icon{
    width: 25px;
    height: 25px;
    background-image: url("../../../assets/icons/email-icon.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
}