.NavBar {
    width: 100vw;
    height: 120px;
    background-color: #000000;
    /* border: 1px solid red; */
    /* background-image: url("../../../assets/header-bg.png");
    background-repeat: no-repeat;
    background-size: 100% 100%; */
    position: sticky;
    top: 0%;
    z-index: 2;
    border-bottom: 2px solid #daa520;
}

.NavBar .logo {
    /* max-width: 460px; */
    width: auto;
    height: 100px;
    /* max-height: 190px; */
    /* background-color: red; */
    background-image: url("../../../assets/logo/logo.png");
    background-repeat: no-repeat;
    /* background-size: 100% 100%; */
    background-size: contain;
    /* font-size: 40px;
    display: flex;
    text-align: center;
    justify-items: center; */

}
.NavBar .title {
    width: auto;
    height: 80px;
    /* background-color: red; */
    background-image: url("../../../assets/logo/title.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    position: absolute;
    top:20px;
    left: 120px;
}
.NavBar .links {
    display: flex;
    position: absolute;
    left: 30vw;
    top: 90px;
    color: #dbdbdb;
    font-size: 18px;
}
.NavBar .links div {
    width: auto;
    margin-right: 20px;
    cursor: pointer;
}


.NavBar .links div:hover {
    transform: scale(1.1);
}
.NavBar .links .inActive {
    color: #dbdbdb;
}
.NavBar .links .active {
    color: #ffe555;
}

@media screen and (max-width: 600px) {
    .NavBar .logo{
        width: 226px;
        height: 70px;
    }
    .NavBar .links div{
        width: auto;
        margin-right: 10px;
        font-size: 16px;
    }
    .NavBar .links{
        top:70px;
        /* right: 10px; */
        left: 10px;
    }
    .NavBar{
        height: 90px;
    }
}